//IcoMoon

@font-face {
    font-family: 'icomoon';
    src:  url('../fonts/icomoon.eot?dpfmnu');
    src:  url('../fonts/icomoon.eot?dpfmnu#iefix') format('embedded-opentype'),
      url('../fonts/icomoon.ttf?dpfmnu') format('truetype'),
      url('../fonts/icomoon.woff?dpfmnu') format('woff'),
      url('../fonts/icomoon.svg?dpfmnu#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
  
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak-as: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
